
























































export default {
	data() {
		return {
			checked_state: -1,
			today: -1,
			speed_day: '0',
			integral_day: '10',
			get_reward: false,
			list: [{
					day: '周一',
					state: false,
				},
				{
					day: '周二',
					state: false,
				}, {
					day: '周三',
					state: true,
				}, {
					day: '周四',
					state: false,
				}, {
					day: '周五',
					state: false,
				}, {
					day: '周六',
					state: false,
				}, {
					day: '周日',
					state: false,
				}
			]
		}
	},
	onLoad() {
		this.integralDayComputer();
	},
	created() {
		this.today = new Date().getDay();
	},
	methods: {
		integralDayComputer() {
			this.speed_day = (this.integral_day / 31) * 100 + "%";
		},
		getReward() {
			this.get_reward = true;
		},
		checked(index, item) {
			if (item.state || this.today != index + 1) {
				return;
			} else {
				this.checked_state = index;
				item.state = true;
				uni.showToast({
					title: '领取成功',
					mask: false,
					duration: 1500
				});
			}
		}
	},
}



